/* global TimelineLite Waypoint jQuery qualys */

/*
 * animate hero content
 */
(function () {

	"use strict";

	//hero animation

	var heroAnimated = false;
	var ease = window.Power4.easeInOut;

	new Waypoint({
		element: document.querySelector(".q-default-hero"),
		offset: "80%",
		handler: function(direction) {
			var t1 = new TimelineLite();
			if (direction === "down" && !heroAnimated) {
				//hero heading and cta animation
				t1.fromTo(".q-default-hero__heading", 1, { y: "10%", autoAlpha: 0 }, { y: "0%", autoAlpha: 1, ease: ease }, "start+=.8");
				t1.fromTo(".q-default-hero__subheading", 1, { y: "10%", autoAlpha: 0 }, { y: "0%", autoAlpha: 1, ease: ease }, "start+=.85");
				t1.fromTo(".hero-cta-wrapper .email-field-wrapper", 1, { y: "10%", autoAlpha: 0 }, { y: "0%", autoAlpha: 1, ease: ease }, "start+=1.15");
				t1.fromTo(".hero-cta-wrapper .hero-cta-quote", 1, { y: "10%", autoAlpha: 0 }, { y: "0%", autoAlpha: 1, ease: ease }, "start+=1.35");

				//hero graphics animation
				t1.fromTo(".item.am", 1, { y: "10%", autoAlpha: 0 }, { y: "0%", autoAlpha: 1, ease: ease }, "start+=1.35");
				t1.fromTo(".plus-1", 1, { y: "10%", autoAlpha: 0 }, { y: "0%", autoAlpha: 1, ease: ease }, "start+=1.40");
				t1.fromTo(".item.vm", 1, { y: "10%", autoAlpha: 0 }, { y: "0%", autoAlpha: 1, ease: ease }, "start+=1.45");
				t1.fromTo(".plus-2", 1, { y: "10%", autoAlpha: 0 }, { y: "0%", autoAlpha: 1, ease: ease }, "start+=1.50");
				t1.fromTo(".item.threat", 1, { y: "10%", autoAlpha: 0 }, { y: "0%", autoAlpha: 1, ease: ease }, "start+=1.55");
				t1.fromTo(".plus-3", 1, { y: "10%", autoAlpha: 0 }, { y: "0%", autoAlpha: 1, ease: ease }, "start+=1.60");
				t1.fromTo(".item.respons", 1, { y: "10%", autoAlpha: 0 }, { y: "0%", autoAlpha: 1, ease: ease }, "start+=1.65");
				t1.fromTo(".details-link", 1, { y: "10%", autoAlpha: 0 }, { y: "0%", autoAlpha: 1, ease: ease }, "start+=1.85");
			}

			heroAnimated = true;
		}
	});

	// VMDR banner animation
	var bannerAnimated = false;

	new Waypoint({
		element: document.querySelector(".vmdr-banner"),
		offset: "80%",
		handler: function(direction) {
			var t2 = new TimelineLite();
			if (direction === "down" && !bannerAnimated) {

				//VMDR banner animation
				t2.fromTo(".vmdr-banner-background", 1, { y: "10%", autoAlpha: 0 }, { y: "0%", autoAlpha: 1, ease: ease }, "start+=.3");
				t2.fromTo(".vmdr-banner-logo", 1, { autoAlpha: 0 }, { autoAlpha: 1, ease: ease }, "start+=.3");
				t2.fromTo(".vmdr-banner-heading", 1, { y: "10%", autoAlpha: 0 }, { y: "0%", autoAlpha: 1, ease: ease }, "start+=0.85");
				t2.fromTo(".vmdr-banner-link", 1, { y: "10%", autoAlpha: 0 }, { y: "0%", autoAlpha: 1, ease: ease }, "start+=1.10");
			}

			bannerAnimated = true;
		}
	});

	// screenshgot animation

	(function () {

		"use strict";

		function setWaypointAndAnimation(wayPointElementId, wayPointOffset, timeLineElementClass, xPosition) {
			var animationStarted = false;
			var t = new TimelineLite();
			var ease = window.Power4.easeInOut;

			new Waypoint({
				element: document.getElementById(wayPointElementId),
				offset: wayPointOffset,
				handler: function(direction) {
					if (direction === "down" && !animationStarted) {
						animationStarted = true;
						t.fromTo(
							timeLineElementClass,
							1,
							{
								x: xPosition,
								autoAlpha: 0
							},
							{
								x: "0%",
								autoAlpha: 1,
								ease: ease
							}
						);
					}
				}
			});
		}

		setWaypointAndAnimation("asset-management", "75%", "#asset-management .screenshot-animated", "25%");
		setWaypointAndAnimation("asset-inventory-screenshot", "75%", "#asset-inventory-screenshot .screenshot-animated", "25%");

		setWaypointAndAnimation("treat-protection-screenshot", "75%", "#treat-protection-screenshot .screenshot-animated", "25%");
		setWaypointAndAnimation("automate-prioritization-screenshot", "75%", "#automate-prioritization-screenshot .screenshot-animated", "25%");

		setWaypointAndAnimation("vulnerability-management-screenshot", "75%", "#vulnerability-management-screenshot .screenshot-animated", "-25%");

		setWaypointAndAnimation("response-screenshot", "75%", "#response-screenshot .screenshot-animated", "-25%");
	}());

}());

(function ($) {

	"use strict";

	$(document).ready(function(){

		$(".js-hero-create-account").on("click", function(){
			$(".email-field-wrapper, .hero-cta-quote").hide();
			$(".marketo-form-wrapper").show();

			var emailAddress = $(this).parent().find(".hero-email").val();
			$("[name=Email]").val( emailAddress );
		});

	});

}(jQuery));

// populate form with email address from URL
(function ($) {
	"use strict";

	$('[type="email"]').val(qualys.parseQueryString(window.location.search).email);

}(jQuery));